.wordSize {
  font-size: 40px;
}

.ticket {
  list-style: none;
}

.ticket li a:hover {
  border-bottom: 1px solid #E31277;
}

.btn-width {
  width: 100%;
}

.mb-40 {
  margin-bottom: 40px;
}

.mt-80 {
  margin-top: 80px;
}

.modal.right .modal-dialog {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  transform: translate3d(0%, 0, 0);
}

.modal.right .modal-content {
  height: 100%;
  overflow-y: auto;
}

.modal-content {
  border-radius: 0;
  border: none;
}

.modal-header {
  border-bottom: none;
}

.modal-body .card {
  max-width: 540px;
}

.modal-footer {
  border-top: none;
}
